import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { useMediaQuery, Divider } from '@material-ui/core';
import { Topbar, Footer, Sidebar } from './components';
import { useStaticQuery } from 'gatsby';
import { graphql } from 'gatsby';
import { truncate } from 'utils/stringUtils';
import { simplifyEdges } from 'utils/graphUtils';

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
  },
}));

const Main = ({ children, themeToggler, themeMode }) => {
  const navigationData = useStaticQuery(graphql`
    query HeaderQuery {
      allMarkdownRemark(sort: {order: DESC, fields: [frontmatter___date]}) {
          edges {
              node {
                  excerpt(pruneLength: 50)
                  id
                  frontmatter {
                      title
                      date(formatString: "MMMM DD, YYYY")
                      path
                  }
              }
          }
      }
    }
  `);

  const posts = simplifyEdges(navigationData);

  const classes = useStyles();

  const theme = useTheme();
  const isMd = useMediaQuery(theme.breakpoints.up('md'), {
    defaultMatches: true,
  });

  const pages = {
    blog: {
      title: 'Blog',
      id: 'blog',
      children: {
        posts: {
          groupTitle: 'Posts',
          pages: [...posts.map(x => ({
            title: truncate(x.title, 20),
            href: x.path
          })), { title: 'ALL POSTS', href: '/blog' }]
        }
      }
    }
  };

  const [openSidebar, setOpenSidebar] = useState(false);

  const handleSidebarOpen = () => {
    setOpenSidebar(true);
  };

  const handleSidebarClose = () => {
    setOpenSidebar(false);
  };

  const open = isMd ? false : openSidebar;

  return (
    <div
      className={clsx({
        [classes.root]: true,
      })}
    >
      <Topbar onSidebarOpen={handleSidebarOpen} pages={pages} themeMode={themeMode} themeToggler={themeToggler} />
      <Sidebar
        onClose={handleSidebarClose}
        open={open}
        variant="temporary"
        pages={pages}
      />
      <main>
        <Divider />
        {children}
      </main>
      <Footer pages={pages} />
    </div>
  );
};

Main.propTypes = {
  children: PropTypes.node,
  themeToggler: PropTypes.func.isRequired,
  themeMode: PropTypes.string.isRequired,
};

export default Main;
